"use client"

import { useState, useEffect } from "react"
import { useContract } from "@/context/contract-context"
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs"
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Skeleton } from "@/components/ui/skeleton"
import { Badge } from "@/components/ui/badge"
import { Bell, Filter, RefreshCw, AlertCircle } from "lucide-react"
import NotificationItem from "./_components/notification-item"
import EmptyState from "./_components/empty-state"

// Define NotificationType enum if it's missing
const NotificationType = {
  CAMPAIGN_CREATED: 0,
  CAMPAIGN_STARTED: 1,
  CAMPAIGN_ENDED: 2,
  VOTE_CAST: 3,
  VOTE_RECEIVED: 4,
  USER_REGISTERED: 5,
  CANDIDATE_REGISTERED: 6
}

export default function ActivityPage() {
  const { contract, address, isConnected, connect } = useContract()
  const [notifications, setNotifications] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [activeTab, setActiveTab] = useState("all")
  const [page, setPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)
  const [refreshing, setRefreshing] = useState(false)
  const ITEMS_PER_PAGE = 10

  const fetchNotifications = async (reset = false) => {
    if (!contract || !address) {
      setLoading(false)
      return
    }

    try {
      const isRefresh = reset && notifications.length > 0
      if (isRefresh) {
        setRefreshing(true)
      } else {
        setLoading(true)
      }
      setError(null)

      const newPage = reset ? 1 : page
      const offset = (newPage - 1) * ITEMS_PER_PAGE

      // Get total count to determine if there are more pages
      const totalCount = await contract.getUserNotificationCount(address)

      // Fetch notifications with pagination
      const fetchedNotifications = await contract.getUserNotifications(address, offset, ITEMS_PER_PAGE)

      // Process notifications
      const processedNotifications = fetchedNotifications.map((notification) => ({
        id: Number(notification.id),
        userAddress: notification.userAddress,
        campaignId: Number(notification.campaignId),
        notificationType: Number(notification.notificationType),
        timestamp: Number(notification.timestamp) * 1000, // Convert to milliseconds
        relatedEntityId: Number(notification.relatedEntityId),
        data: notification.data,
      }))

      // Update state
      if (reset) {
        setNotifications(processedNotifications)
      } else {
        setNotifications((prev) => [...prev, ...processedNotifications])
      }

      setPage(newPage + 1)
      setHasMore(offset + ITEMS_PER_PAGE < Number(totalCount))
      
    } catch (err) {
      console.error("Error fetching notifications:", err)
      setError("Failed to load notifications. Please try again.")
    } finally {
      setLoading(false)
      setRefreshing(false)
    }
  }

  useEffect(() => {
    if (isConnected) {
      fetchNotifications(true)
    } else {
      setNotifications([])
      setLoading(false)
    }
  }, [isConnected, address, contract])

  const handleRefresh = () => {
    fetchNotifications(true)
  }

  const handleLoadMore = () => {
    if (!loading && hasMore) {
      fetchNotifications()
    }
  }

  const filterNotifications = (type) => {
    if (type === "all") return notifications

    return notifications.filter((notification) => {
      switch (type) {
        case "campaigns":
          return [
            NotificationType.CAMPAIGN_CREATED,
            NotificationType.CAMPAIGN_STARTED,
            NotificationType.CAMPAIGN_ENDED,
          ].includes(notification.notificationType)
        case "votes":
          return [NotificationType.VOTE_CAST, NotificationType.VOTE_RECEIVED].includes(notification.notificationType)
        case "registrations":
          return [NotificationType.USER_REGISTERED, NotificationType.CANDIDATE_REGISTERED].includes(
            notification.notificationType,
          )
        default:
          return true
      }
    })
  }

  if (!isConnected) {
    return (
      <div className="container max-w-5xl py-10">
        <Card className="border-dashed">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Bell className="h-5 w-5" />
              Activity
            </CardTitle>
            <CardDescription>Connect your wallet to view your activity</CardDescription>
          </CardHeader>
          <CardContent className="flex flex-col items-center justify-center py-10">
            <div className="bg-muted/20 rounded-full p-5 mb-4">
              <Bell className="h-16 w-16 text-muted-foreground/30" />
            </div>
            <h3 className="text-lg font-medium mb-2">No Connected Account</h3>
            <p className="text-muted-foreground text-center max-w-md">
              Connect your wallet to view your notifications and activity history
            </p>
            <Button className="mt-6" onClick={connect}>Connect Wallet</Button>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="container max-w-5xl py-10">
      <Card>
        <CardHeader className="flex flex-row items-center justify-between">
          <div>
            <CardTitle className="flex items-center gap-2">
              <Bell className="h-5 w-5" />
              Activity
            </CardTitle>
            <CardDescription>Your recent activity and notifications</CardDescription>
          </div>
          <Button variant="outline" size="icon" onClick={handleRefresh} disabled={loading || refreshing}>
            <RefreshCw className={`h-4 w-4 ${(loading || refreshing) ? "animate-spin" : ""}`} />
            <span className="sr-only">Refresh</span>
          </Button>
        </CardHeader>
        <CardContent>
          <Tabs defaultValue="all" value={activeTab} onValueChange={setActiveTab}>
            <div className="flex items-center justify-between mb-6">
              <TabsList className="w-full sm:w-auto">
                <TabsTrigger value="all" className="flex-1 sm:flex-initial">All</TabsTrigger>
                <TabsTrigger value="campaigns" className="flex-1 sm:flex-initial">Campaigns</TabsTrigger>
                <TabsTrigger value="votes" className="flex-1 sm:flex-initial">Votes</TabsTrigger>
                <TabsTrigger value="registrations" className="flex-1 sm:flex-initial">Registrations</TabsTrigger>
              </TabsList>

              <Badge variant="outline" className="hidden sm:flex items-center gap-1">
                <Filter className="h-3 w-3" />
                <span>{activeTab === "all" ? "All activities" : `Filtered: ${activeTab}`}</span>
              </Badge>
            </div>

            <TabsContent value="all" className="mt-0">
              {renderNotificationList(filterNotifications("all"))}
            </TabsContent>

            <TabsContent value="campaigns" className="mt-0">
              {renderNotificationList(filterNotifications("campaigns"))}
            </TabsContent>

            <TabsContent value="votes" className="mt-0">
              {renderNotificationList(filterNotifications("votes"))}
            </TabsContent>

            <TabsContent value="registrations" className="mt-0">
              {renderNotificationList(filterNotifications("registrations"))}
            </TabsContent>
          </Tabs>

          {error && (
            <div className="p-4 bg-red-50 dark:bg-red-900/10 border border-red-200 dark:border-red-900/20 rounded-lg mt-4 flex flex-col items-center text-center">
              <AlertCircle className="h-8 w-8 text-red-500 mb-2" />
              <p className="text-red-600 dark:text-red-400">{error}</p>
              <Button variant="outline" onClick={handleRefresh} className="mt-3">
                Try Again
              </Button>
            </div>
          )}
        </CardContent>
        
        {hasMore && !loading && notifications.length > 0 && (
          <CardFooter className="flex justify-center border-t pt-4">
            <Button variant="outline" onClick={handleLoadMore} disabled={loading} className="w-full sm:w-auto">
              {loading ? "Loading..." : "Load More Notifications"}
            </Button>
          </CardFooter>
        )}
        
        {loading && !refreshing && (
          <CardFooter className="border-t pt-4">
            <div className="space-y-4 w-full">
              {[...Array(3)].map((_, i) => (
                <div key={i} className="flex items-start gap-4">
                  <Skeleton className="h-10 w-10 rounded-full flex-shrink-0" />
                  <div className="space-y-2 flex-1">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-3/4" />
                    <div className="flex justify-between mt-2">
                      <Skeleton className="h-8 w-24" />
                      <Skeleton className="h-8 w-16" />
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </CardFooter>
        )}
      </Card>
    </div>
  )

  function renderNotificationList(filteredNotifications) {
    if (filteredNotifications.length === 0 && !loading) {
      return <EmptyState type={activeTab} />
    }
  
    return (
      <div className="space-y-4">
        {filteredNotifications.map((notification) => (
          <NotificationItem key={notification.id} notification={notification} />
        ))}
      </div>
    )
  }
}